import React from 'react'

// responsiveView: desktop, tablet, mobile
export default props => {
  const responsiveView = props.responsiveView

  const styles = {
    position: 'absolute',
    top: 0,
    right: 0,
    left: 0,
    bottom: 0,
    margin: 'auto',
    zIndex: 500,
    width: '100%',
    transition: '300ms'
  }

  if (responsiveView === 'tablet') {
    styles.width = 768
    styles.maxHeight = 1024
  }

  else if (responsiveView === 'mobile') {
    styles.width = 360
    styles.maxHeight = 640
  }

  return (
    <div style={styles}>
      {props.children}
    </div>
  )
}



