import { connect } from 'react-redux'
import Iframe from '../components/Iframe/Iframe'
import urlSelector from '../state/template/url/selector'
import iframeLoadedSelector from '../state/template/iframeLoaded/selector'
import { setLoaded } from '../state/template/iframeLoaded/actions'
import { setProperties } from '../state/site/properties/actions'

const $ = window.jQuery

const mapStateToProps = (state) => ({
  src: urlSelector(state),
  isActive: iframeLoadedSelector(state)
})

const mapDispatchToProps = (dispatch) => ({
  onLoad: (iframeElem) => {
    const $iframe = $(iframeElem).eq(0).contents()

    // get global store
    iframeElem.contentWindow.store = window.__store

    // attach store to iframe too
    iframeElem.contentWindow.store.subscribe(() => {
      const state = iframeElem.contentWindow.store.getState()

      // site title
      $iframe.find('[data-replacement]').text(state.site.title)

      // site color
      $iframe
        .find('body')
          .removeClass((index, css)  => (css.match (/(^|\s)COLOR--\S+/g) || []).join(' '))
          .addClass(`COLOR--${state.site.color}`)

      // site properties: text
      $iframe.find('[data-property="text"]').each((index, elem) => {
        const $elem = $(elem)
        const group = $elem.attr('data-group')
        const key = $elem.attr('data-key')
        
        // get related property
        const property = state.site.properties.find(property => {
          return property.Group == group && property.Key == key
        })

        // change element text
        if (property) {
          $elem.text(property.Value)
        }
      })

    })

    $iframe.find('[data-property="text"]').each((index, elem) => {
      const $elem = $(elem)
      const group = $elem.attr('data-group')
      const key = $elem.attr('data-key')

      // make it editable
      $elem.prop('contenteditable', true)

      $elem.addClass('__tredestone_highlight')

      const css = `
        .__tredestone_highlight {
          position: relative;
        }

        .__tredestone_highlight::before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          border: 1px solid rgba(56, 153, 236, 0.6);
        }

        .__tredestone_highlight:focus::before,
        .__tredestone_highlight:hover::before {
          border-color: rgba(56, 153, 236, 1);
        }
      `

      const head = $iframe.find('head')[0]
      const style = document.createElement('style')

      style.type = 'text/css'
      if (style.styleSheet) {
        style.styleSheet.cssText = css
      } else {
        style.appendChild(document.createTextNode(css))
      }

      head.appendChild(style)


      $elem.on('blur', e => {
        const state = iframeElem.contentWindow.store.getState()
        const properties = state.site.properties.map(property => {
        const newProperty = Object.assign({}, property)
          return newProperty
        })

        const property = properties.find(property => {
          return property.Group == group && property.Key == key
        }) || {
          Group: '',
          Key: '',
          Value: ''
        }
        
        const newValue = $(elem).text()
        property.Value = newValue
        dispatch(setProperties(properties))
      })
    })

    dispatch(setLoaded(true))
  },

  onBeforeLoad: () => {
    dispatch(setLoaded(false))
  }
})

export default connect(mapStateToProps, mapDispatchToProps)(Iframe)



