import { connect } from 'react-redux'
import ResponsiveView from '../components/ResponsiveView/ResponsiveView'
import { changeView } from '../state/responsiveView/viewName/actions'
import selector from '../state/responsiveView/viewName/selector'

const mapStateToProps = (state, ownProps) => ({
  isActive: selector(state) === ownProps.icon
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  onClick: () => dispatch(changeView(ownProps.icon))
})

export default connect(mapStateToProps, mapDispatchToProps)(ResponsiveView)



