import { connect } from 'react-redux'
import QueryString from 'qs'

import notify from '../app/notify'
import ajax from '../app/ajax'
import SaveProperties from '../components/SaveProperties/SaveProperties'
import selector from '../state/site/properties/selector'
import { setErrorMessage } from '../state/error/message/actions'
import config from '../config'
import { normalizeErrorMessage } from '../app/utility'

const mapStateToProps = (state) => ({
  properties: selector(state),
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  onSave: properties => {
    const qs = QueryString.parse(window.location.search.substr(1))

    const request = ajax.post(config.apiUrls.UpdateProperties, {
      siteId: qs.siteId,
      properties: properties,
    })

    request.catch(error => {
      const message = normalizeErrorMessage(error)
      dispatch(setErrorMessage(message))
    })

    request.then(json => {
      notify.success(json.data && json.data.Message)
    })

    return request
  }
})

export default connect(mapStateToProps, mapDispatchToProps)(SaveProperties)



