import { connect } from 'react-redux'
import ResponsivePositioner from '../components/ResponsivePositioner/ResponsivePositioner'
import selector from '../state/responsiveView/viewName/selector'

const mapStateToProps = (state) => ({
  responsiveView: selector(state)
})

export default connect(mapStateToProps)(ResponsivePositioner)



