import { combineReducers } from 'redux'
import responsiveView from '../state/responsiveView/_root/reducer'
import template from '../state/template/_root/reducer'
import site from '../state/site/_root/reducer'
import error from '../state/error/_root/reducer'

const rootReducer = combineReducers({
  responsiveView,
  template,
  site,
  error,
})

export default rootReducer



