import React, { Component } from 'react'
import styles from './MenuBar.css'
import logo from './logo.png'
import ResponsiveViewContainer from '../../containers/ResponsiveViewContainer'
import SavePropertiesModule from '../../modules/SavePropertiesModule'
import resource from '../../resource'

class MenuBar extends Component {
  render() {
    return (
      <div className={styles.MenuBar}>
        <div className={styles.MenuLogo}>
          <span className={styles.LogoLink}><img alt="" id={styles.Logo} src={logo} /></span>
        </div>
        <div className={styles.MenuItems}>
          <span className={styles.MenuItemsSpecial}>
            <i className="fa fa-file-text-o" aria-hidden="true"></i>
            <span>{resource.TEXT_PAGE}: {resource.TEXT_HOME}</span>
            <i className="fa fa-angle-down" aria-hidden="true"></i>
          </span>
          <span>{resource.TEXT_BACK_TO_DASHBOARD}</span>
          {/*
          <span><i className="fa fa-angle-down"></i> 
            <ul>
              <li> </li>
              <li> </li>
              <li> </li>
            </ul>
          </span>
          <span></span>
          <span></span>
          <span></span>
          */}
        </div>
        <div className={styles.MenuAsset}>
          <ResponsiveViewContainer icon="mobile" activeClassName={styles.active} />
          <ResponsiveViewContainer icon="tablet" activeClassName={styles.active} />
          <ResponsiveViewContainer icon="desktop" activeClassName={styles.active} />

          <span>{resource.TEXT_PREVIEW}</span>

          <SavePropertiesModule />

          <span className={styles['MenuAsset-important']}>{resource.TEXT_PUBLISH}</span>
        </div>
      </div>
    )
  }
}

export default MenuBar



