import React, { Component } from 'react'

import resource from '../../resource'

class SettingTitle extends Component {
  constructor(props) {
    super(props)

    this.handleChange = this.handleChange.bind(this)
    this.onCancel = this.onCancel.bind(this)
    this.onSave = this.onSave.bind(this)

    this.state = {
      value: this.props.value,
      loading: false
    }
  }

  componentWillReceiveProps(nextProps) {
    this.setState({
      value: nextProps.value
    })
  }

  handleChange(e) {
    const newValue = e.target.value
    this.setState({
      value: newValue
    })
  }

  onCancel() {
    this.setState((prevState, props) => ({
      value: props.value
    }))
  }

  onSave(e) {
    e.preventDefault()

    this.setState({ loading: true })

    const done = () => {
      this.setState({ loading: false })
    }

    this.props.onSave(this.state.value)
      .catch(done)
      .then(done)
  }

  render() {
    return (
      <div>
        <form onSubmit={this.onSave}>
          <hr />
          <div style={{ margin: '10px 0' }}><input type="text" className="form-control" value={this.state.value} onChange={this.handleChange} /></div>
          <div style={{ margin: '10px 0' }}><button type="submit" className="btn btn-success btn-block">{resource.TEXT_SAVE}</button></div>

          {! this.state.loading && this.props.value !== this.state.value &&
            <div style={{ margin: '10px 0' }}>
              <button type="button" className="btn btn-danger btn-block" onClick={this.onCancel}>{resource.TEXT_CANCEL}</button>
            </div>
          }

          {/*! this.state.loading && this.props.value !== this.state.value && <div>
            <hr />
                
          </div>*/}

          {this.state.loading && <div className="text-center">
            <i className="fa fa-spin fa-spinner fa-2x"></i>
          </div>}
        </form>
      </div>
    )
  }
}

export default SettingTitle



