import { connect } from 'react-redux'
import QueryString from 'qs'

import notify from '../app/notify'
import ajax from '../app/ajax'
import SettingTitle from '../components/SettingTitle/SettingTitle'
import selector from '../state/site/title/selector'
import { setTitle } from '../state/site/title/actions'
import { setErrorMessage } from '../state/error/message/actions'
import config from '../config'
import { normalizeErrorMessage } from '../app/utility'

const mapStateToProps = (state) => ({
  value: selector(state),
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  onSave: value => {
    const qs = QueryString.parse(window.location.search.substr(1))

    const request = ajax.post(config.apiUrls.SetSiteTitle, {
      siteId: qs.siteId,
      title: value,
    })

    request.catch(error => {
      const message = normalizeErrorMessage(error)
      dispatch(setErrorMessage(message))
    })

    request.then(json => {
      dispatch(setTitle(value))

      if (typeof ownProps.onSaveCallback === typeof Function) {
        ownProps.onSaveCallback()
      }

      notify.success(json.data && json.data.Message)
    })

    return request
  }
})

export default connect(mapStateToProps, mapDispatchToProps)(SettingTitle)



