import axios from 'axios'

const ajax = {
  'get': (url, params) => axios.get(url, { params }),
  'post': (url, params) => axios.post(url, params)
}

if (process.env.NODE_ENV === 'development') {
  const request = (url, params) => new Promise((resolve, reject) => {
    const ajax = axios.get(url, { params })
    setTimeout(() => {
      resolve(ajax)
    }, 100)
  })

  ajax.get = request
  ajax.post = request
}

export default ajax



