import React, { Component } from 'react'
import { connect } from 'react-redux'
import QueryString from 'qs'
import ajax from '../app/ajax'

import { setUrl } from '../state/template/url/actions'
import { setColors } from '../state/template/colors/actions'
import { setTitle } from '../state/site/title/actions'
import { setColor } from '../state/site/color/actions'
import { setProperties } from '../state/site/properties/actions'
import { setErrorMessage } from '../state/error/message/actions'
import config from '../config'
import { normalizeErrorMessage } from '../app/utility'

class EditorModule extends Component {
  constructor(props) {
    super(props)

    this.onTemplateLoad = this.onTemplateLoad.bind(this)
    this.onSiteLoad = this.onSiteLoad.bind(this)
  }

  componentDidMount() {
    // get site
    const qs = QueryString.parse(window.location.search.substr(1))
    const request = ajax.get(config.apiUrls.GetSiteById, {
      siteId: qs.siteId
    })

    request.catch(error => {
      const message = normalizeErrorMessage(error)
      this.props.onAjaxError(message)
    })

    request.then(json => {
      this.onSiteLoad(json.data)
    })
  }

  onSiteLoad(site) {
    // dispatch needed actions for store
    this.props.onSiteLoad(site)

    // load template
    const request = ajax.get(config.apiUrls.GetTemplateById, {
      templateId: site.TemplateId
    })

    request.catch(error => {
      const message = normalizeErrorMessage(error)
      this.props.onAjaxError(message)
    })

    request.then(json => {
      this.onTemplateLoad(json.data)
    })
  }

  onTemplateLoad(template) {
    // dispatch needed actions for store
    this.props.onTemplateLoad(template)
  }
  
  render() {
    return this.props.children
  }
}

const mapStateToProps = (state, ownProps) => ({
  
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  onTemplateLoad: (template) => {
    const qs = QueryString.parse(window.location.search.substr(1))
    const page = qs.page || 'about'
    const basename = template.Basename

    const url = `./templates/${basename}/${page}.html`

    dispatch(setUrl(url))
    dispatch(setColors(template.Colors))
  },

  onSiteLoad: (site) => {
    // site title
    dispatch(setTitle(site.Title))

    // site properties
    dispatch(setProperties(site.Properties))

    // set current color on state
    const color = site.Properties.find(property => {
      return (property.Group == 'Core' && property.Key == 'SiteColor')
    }).Value
    
    if (color) {
      dispatch(setColor(color))
    }
  },

  onAjaxError: message => {
    dispatch(setErrorMessage(message))
  }
})

export default connect(mapStateToProps, mapDispatchToProps)(EditorModule)



