import React, { PropTypes } from 'react'
import styles from './Setting.css'
import cx from 'classnames'

const SettingPanel = (props) => {
  return (
    <div className={cx(styles.SettingOpenedPanel, styles.PanelTop)} style={{width: props.width, height: props.height}}>
      <div className={styles.SettingBodyContainer}>
        {/*
        <div className={styles.SettingExtraColumn} style={{width: 150}}>
          
        </div>
        */}
        <div className={styles.SettingPanelHeader}>
          <span className={styles.SettingPanelTitle}>{props.title}</span>
          <div className={styles.SettingPanelButtons}>
            <span><i className="fa fa-fw fa-times" onClick={props.onClose}></i></span>
            {props.onHelp && <span><i className="fa fa-fw fa-question" onClick={props.onHelp}></i></span>}
          </div>
        </div>
        <div className={styles.SettingPanelBody}>
          {props.children}
        </div>			
      </div>
    </div>
  )
}

SettingPanel.propTypes = {
  title: PropTypes.string,
  icon: PropTypes.string,
  onClose: PropTypes.func,
  onHelp: PropTypes.func,
  width: PropTypes.string,
  height: PropTypes.string
}

SettingPanel.defaultProps = {
  width: '300px',
  height: '500px'
}

export default SettingPanel



