// incomplete
// the redux-saga doesn't implement yet

import { put, call, takeLatest } from 'redux-saga/effects'
import axios from 'axios'

import config from '../config'

function fetchSiteAjax(params) {
  return axios.get(config.apiUrls.GetSiteById, {
    siteId: 'sample-site-id'
  })
}

function* fetchSite(action) {
  yield put({type: 'site/api/loading', payload: true})

  try {
    const user = yield call(fetchSiteAjax)
    console.log(user)
    yield put({type: 'site/api/succeeded', user: user})
  } catch (e) {
    console.error(e)
    yield put({type: 'site/api/failed', payload: { message: e.message }})
  }
}

function* mySaga() {
  yield takeLatest('site/api/fetch', fetchSite)
}

export default mySaga



