import { connect } from 'react-redux'
import QueryString from 'qs'

import notify from '../app/notify'
import ajax from '../app/ajax'
import SettingColor from '../components/SettingColor/SettingColor'
import selector from '../state/template/colors/selector'
import siteColorSelector from '../state/site/color/selector'
import { setColor } from '../state/site/color/actions'
import { setErrorMessage } from '../state/error/message/actions'
import config from '../config'
import { normalizeErrorMessage } from '../app/utility'

const mapStateToProps = state => ({
  colors: selector(state),
  color: siteColorSelector(state),
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  onSave: value => {
    const qs = QueryString.parse(window.location.search.substr(1))

    const request = ajax.post(config.apiUrls.SetSiteColor, {
      siteId: qs.siteId,
      color: value,
    })

    request.catch(error => {
      const message = normalizeErrorMessage(error)
      dispatch(setErrorMessage(message))
    })

    request.then(json => {
      dispatch(setColor(value))

      if (typeof ownProps.onSaveCallback === typeof Function) {
        ownProps.onSaveCallback()
      }

      notify.success(json.data && json.data.Message)
    })

    return request
  }
})

export default connect(mapStateToProps, mapDispatchToProps)(SettingColor)



