const apiUrlPrefix = './api/'
const apiUrlSuffix = '.json'

export default {
  apiUrls: {
    GetSiteById: `${apiUrlPrefix}GetSiteById${apiUrlSuffix}`,
    GetTemplateById: `${apiUrlPrefix}GetTemplateById${apiUrlSuffix}`,
    SetSiteTitle: `${apiUrlPrefix}SetSiteTitle${apiUrlSuffix}`,
    SetSiteColor: `${apiUrlPrefix}SetSiteColor${apiUrlSuffix}`,
    UpdateProperties: `${apiUrlPrefix}UpdateProperties${apiUrlSuffix}`,
  }
}



