import React, { Component } from 'react'
import SweetAlert from 'sweetalert-react'

import MenuBar from '../MenuBar/MenuBar'
import Setting from '../Setting/Setting'
import Styled from '../Styled/Styled'
import ErrorMessage from '../ErrorMessage/ErrorMessage'
import ErrorContainer from '../../containers/ErrorContainer'
import IframeModule from '../../modules/IframeModule'
import ResponsivePositionerContainer from '../../containers/ResponsivePositionerContainer'
import EditorModule from '../../modules/EditorModule'

class App extends Component {
  componentDidMount() {
    // remove the loader
    const loader = document.getElementById('loader')
    if (loader) {
      loader.parentNode.removeChild(loader)
    }
  }
  
  render() {
    return (
      <EditorModule>
        <div>
        
          <Styled position="absolute" top="0" right="0" left="0" zIndex="1000">
            <MenuBar />
          </Styled>

          <Styled position="absolute" top="80px" right="30px" width="150px" zIndex="1000">
            <Setting />
          </Styled>

          <Styled position="absolute" top="48px" right="0" left="0" bottom="0" zIndex="900">
            <ResponsivePositionerContainer>
              <IframeModule />
            </ResponsivePositionerContainer>
          </Styled>

          <ErrorContainer>
            <ErrorMessage />
          </ErrorContainer>
          
        </div>
      </EditorModule>
    )
  }
}

export default App



