import React, { Component } from 'react'
import cx from 'classnames'

import styles from '../MenuBar/MenuBar.css'
import resource from '../../resource'

class SaveProperties extends Component {
  constructor(props) {
    super(props)

    this.onSave = this.onSave.bind(this)

    this.state = {
      loading: false
    }
  }

  onSave(e) {
    e.preventDefault()

    this.setState({ loading: true })

    const done = () => {
      this.setState({ loading: false })
    }

    this.props.onSave(this.props.properties)
      .catch(done)
      .then(done)
  }

  render() {
    return (
      <span onClick={this.onSave}>
        {resource.TEXT_SAVE}

        {this.state.loading && <span className={styles.MenuProgressContainer}>
          <div className={styles.progress}>
            <div className={cx('progress-bar progress-bar-success progress-bar-striped', styles.ProgressBarActive)} style={{width: '100%'}}></div>
          </div>
        </span>}
      </span>
    )
  }
}

export default SaveProperties



