import React, { Component } from 'react'
import Circle from '../Circle/Circle'

class SettingColor extends Component {
  constructor(props) {
    super(props)

    this.onSave = this.setColor.bind(this)

    this.state = {
      color: this.props.color,
      loading: false
    }
  }

  setColor(color) {
    this.setState({
      color: color
    })

    this.setState({ loading: true })

    const done = () => {
      this.setState({ loading: false })
    }

    this.props.onSave(color)
      .catch(done)
      .then(done)
  }

  componentWillReceiveProps(nextProps) {
    this.setState({
      color: nextProps.color
    })
  }

  render() {
    const colors = this.props.colors

    return (
      <div>

        <div style={{ width: '220px', margin: '20px auto' }}>
          {Object.keys(colors).map(key => 
            <span onClick={() => this.setColor(key)} key={key}>
              <Circle color={colors[key]} isActive={key === this.props.color} />
            </span>
          )}
        </div>

        {this.state.loading && <div className="text-center" style={{ marginTop: '20px'}}>
          <i className="fa fa-spin fa-spinner fa-2x"></i>
        </div>}
        
      </div>
    )
  }
}

export default SettingColor



