import React, { Component } from 'react'
import SettingPanel from './SettingPanel'
import SettingTitleModule from '../../modules/SettingTitleModule'
import SettingColorModule from '../../modules/SettingColorModule'
import styles from './Setting.css'
import cx from 'classnames'
import resource from '../../resource'

class Setting extends Component {
  constructor(props) {
    super(props)

    this.state = {
      active: ''
    }
  }

  setActivePanel(panel) {
    this.setState({
      active: panel
    })
  }

  setFont(font) {
    const $ = window.jQuery
    const $iframe = $('iframe').eq(0).contents()

    $iframe
      .find('body')
        .removeClass((index, css)  => (css.match (/(^|\s)FONT--\S+/g) || []).join(' '))
        .addClass(`FONT--${font}`)
  }

  render() {
    return (
      <div className={cx(styles.Setting, this.state.active === '' ? styles.SettingBarred : '')}>
        <div className={styles.SettingIconContainer}>

          <div className={cx(styles.SettingBlue, this.state.active === 'title' ? styles.active : '' )}>
            <div className={styles.SettingColorInner}>
              <i className="fa fa-fw fa-header" onClick={() => this.setActivePanel('title')}></i>
              <span className={styles.SettingIconTitle}>{resource.SETTING_PANEL_SITE_TITLE_HEAD}</span>

              <SettingPanel
                title={resource.SETTING_PANEL_SITE_TITLE_HEAD}
                onClose={() => this.setActivePanel('')}
              >
                <div>{resource.SETTING_PANEL_SITE_TITLE_INFO}</div>
                <SettingTitleModule _onSaveCallback={() => this.setActivePanel('')} />
              </SettingPanel>
            </div>
          </div>
        
          {/* <div className={cx(styles.SettingPink, this.state.active === 'font' ? styles.active : '' )}>
            <div className={styles.SettingColorInner}>
              <i className="fa fa-fw fa-font" onClick={() => this.setActivePanel('font')}></i>
              <span className={styles.SettingIconTitle}></span>

              <SettingPanel
                title=" "
                onClose={() => this.setActivePanel('')}
                onHelp={() => console.log('help font clicked')}
              >
                <div onClick={() => this.setFont('Vazir')}></div>
                <div onClick={() => this.setFont('Sahel')}></div>
                <div onClick={() => this.setFont('Gandom')}></div>
                <div onClick={() => this.setFont('Shabnam')}></div>
              </SettingPanel>
            </div>
          </div> */ }

          <div className={cx(styles.SettingRed, this.state.active === 'color' ? styles.active : '' )} data-style={{pointerEvents: 'none'}}>
            <div className={styles.SettingColorInner}>
              <i className="fa fa-fw fa-eyedropper" onClick={() => this.setActivePanel('color')}></i>
              <span className={styles.SettingIconTitle}>{resource.SETTING_PANEL_SITE_COLOR_HEAD}</span>

              <SettingPanel
                title={resource.SETTING_PANEL_SITE_COLOR_HEAD}
                onClose={() => this.setActivePanel('')}
              >
                <SettingColorModule />
              </SettingPanel>
            </div>
          </div>

        </div>
      </div>
    )
  }
}

export default Setting



