import React, { PropTypes } from 'react'
import SweetAlert from 'sweetalert-react'

const ErrorMessage = ({
  text = '',
  show = false,
  onConfirm = () => {}
}) => {
  return (
    <SweetAlert
      title=""
      type="error"
      confirmButtonText=" "
      show={show}
      text={text}
      onConfirm={onConfirm}
    />
  )
}

ErrorMessage.propTypes = {
  message: PropTypes.string,
  show: PropTypes.bool,
}

export default ErrorMessage



